<template>
    <div class="container">
      <waterfall
              :line-gap="200"
              :watch="info.data"
      >
        <waterfall-slot
                v-for="(image, i) in info.data"
		:key="i"
 >          <img :src=image />
        </waterfall-slot>
      </waterfall>
    </div>
</template>

<script>
import axios from "axios";
import Waterfall from 'vue-waterfall/lib/waterfall'
import WaterfallSlot from 'vue-waterfall/lib/waterfall-slot'

export default {
  name: 'App',
  components: {
    Waterfall,
    WaterfallSlot

  },
methods: {
    OnEnd: function() {
      console.log(this.info);
}

},
  data () {
    return {
      info: []
    }
  },
  mounted () {
    axios
      .get('https://mpengs.nyc/get_event_pics')
      .then(response => (
	this.info = response.data
      ))
 
 }
}
</script>

